import React from 'react';
import * as UI from '../components/all';
import {Button} from 'reactstrap';

const Demo = (props) =>{
    return(
        <div className="feature-page">
            {props.pageLog.map((item,i) => {
               let ItemType = UI[item.el]
               return (
               <div style={{position: 'relative'}} key={i}>
                    <ItemType  data={item.data} key={i}/>
                    <Button onClick={() => props.onItemRemove(i)} style={{position: 'absolute', top: 20, right: 20}} color="danger">Remove</Button>
               </div>)
            })} 
             <div dangerouslySetInnerHTML={{__html: '<script>var hello = true</script>'}}></div>
        </div>
    )
}

export default Demo;