import React, { Component } from 'react';
import {Modal} from 'reactstrap';
import Demo from '../templates/Demo'



export class PageBuilder extends Component{
    constructor (props) {
        super(props)
        this.state = {
            htmlEL: '',
            toggle: false,
            toggle2: false,
            pageLog:[]

        }
        this.container = React.createRef()
        this.onItemRemove = this.onItemRemove.bind(this)
    }

    getHtml = () => {
        navigator.clipboard.writeText(this.container.current.innerHTML)
            this.setState({
                htmlEL: this.container.current.innerHTML
            })
        this.toggle()
       
    }
    toggle = () => {
        this.setState({
            toggle: !this.state.toggle
        })
        
    }
    toggle2 = () => {
        this.setState({
            toggle2: !this.state.toggle2
        })
    }
    pageType =(el) => {
        console.log(el)
        this.setState ( {
            pageLog:  [ ...this.state.pageLog, 
                {
                    el: el,
                    // data: false
                }
            ]
        }, ()=> {
            if(this.container){
                this.container.current.scrollIntoView({behavior: 'smooth', block: 'end'})
            }
        })
    }

    onItemRemove = (i) => {
        let log = this.state.pageLog;
        console.log(i)
        if(i === 0){
            log.shift()
        }else{
            log.splice(i, 1);
        }
        this.setState({
            pageLog: log,
        })
    }

    render(){
        return (
            <div style={{paddingTop: 80}}>
                <div ref={this.container} >
                    <Demo pageLog={this.state.pageLog} onItemRemove={this.onItemRemove}/>
                </div>
                <div >
                    <button style={{position:'fixed', top: 20, left: 50,padding: '10px 25px', border: 0, borderRadius: '4px', backgroundColor: '#1b1b1b', color: 'white', fontSize: '16px', fontWeight: 'bold'}} onClick={this.toggle2}>Menu</button>
                    <div className={'custom-slide slide-' + (this.state.toggle2 ? 'show' : '')}>
                        <ul>
                            <p style={{fontWeight: 'bold'}}onClick={this.toggle2}>CLOSE</p>
                            <br />
                            <li onClick={() => this.pageType('Hero')}>Hero</li>
                            <li  onClick={() => this.pageType('MenuContainer')}>Menu</li>
                        </ul>
                    </div>
                    <button style={{position:'fixed', top: 20, right: 50,padding: '10px 25px', border: 0, borderRadius: '4px', backgroundColor: '#1b1b1b', color: 'white', fontSize: '16px', fontWeight: 'bold'}} onClick={this.getHtml}>Get Code</button>
                    <Modal toggle={this.toggle} isOpen={this.state.toggle} style={{maxWidth: '800px', padding: '20px'}}>
                        <h1>This is what gets copied</h1>
                        <br/>
                        <div style={{padding: '10px'}}>
                            {this.state.htmlEL}
                        </div>
                    </Modal>
                </div>
            </div>
        )
    }
}

